import React, { Component } from "react";
import "./LocationDetail.css";

class LocationDetail extends Component {
  state = {};
  render() {
    return (
      <div className="locationDetail">
        <p>city: {this.props.state.City}</p>
        <p>country: {this.props.state.country}</p>
        <p>condition: {this.props.state.con}</p>
        <p>temp: {this.props.state.temp}</p>
        <p>humidity: {this.props.state.humidity}</p>
        <img src={this.props.state.image} alt="" />
      </div>
    );
  }
}

export default LocationDetail;
